.Nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding:10px;
  }

  .Nav .Logo img {
    max-height: 100px;
    max-width: 100px;
  }

  .Nav span {
    margin-right: 10px;
    font-weight: bold;
    font-size: 20px;
  }
.login {
  line-height: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}
.login span {
  margin-top: 5px;
}
.login button {
  
 line-height: 20px!important;
 padding: 5px;
}
.login button:hover {
  font-weight: 700;
}

.login a {
  color: black;
}

@media screen and (max-width:768px){
  .Logo {
    visibility: hidden;
  }
}

