.margin {
  margin-top: 10px;
  margin-right: 10px;
  border: 1px solid;
}
.ActivePage {
  font-weight: bold;
  margin-right: 10px;
}

.Prev {
  margin-top: 10px;
  margin-right: 10px;
}

@media screen and (max-width:768px) {
  .Prev {
    margin-top: 10px;
  }
  .margin span {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .pagination {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .pagination span {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
}